/**
 * Icon <uo-icon>
 *
 * Attributes:
 * id  The ID of the icon
 */

// Import global styles
// This will load the font
import './style.scss';

// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';

// Import style for web component
const styles = require( './web-component-style.scss' );

// eslint-disable-next-line require-jsdoc
export class AutomatorIcon extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// eslint-disable-next-line require-jsdoc
	static properties = {
		id: { type: String, reflect: true }
	};

	// eslint-disable-next-line require-jsdoc
	constructor() {
		super();
	}

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			<div
				class="icon"
				id="${ this.id }"
				?is-integration="${ this.integrations.includes( this.id ) }"
			></div>
		`;
	}

	/**
	 * List of supported integrations
	 *
	 * @return {Array} Integrations
	 */
	get integrations() {
		return [
			'active-campaign',
			'facebook',
			'google-calendar',
			'google-sheets',
			'google',
			'hubspot',
			'instagram',
			'mailchimp',
			'slack',
			'twitter',
			'zoom',
			'twilio',
			'gotowebinar',
			'gototraining',
			'whatsapp',
			'linkedin'
		];
	}
}

customElements.define( 'uo-icon', AutomatorIcon );
